import React, { useState } from 'react'
import { NavLink } from 'react-router-dom'
import styles from './Sidebar.module.css'
import VirtualAiLogo from '../../assets/icons/VirtualAiLogo.png'
import VaiLogo from '../../assets/icons/VAILogo.png'
import monitoring from '../../assets/icons/Monitoring.png'
import analytics from '../../assets/icons/Analytics.png'
import workers from '../../assets/icons/Workers.png'
import credentials from '../../assets/icons/Credentials.png'
import process from '../../assets/icons/Process.png'
import settings from '../../assets/icons/Settings.png'
import search from '../../assets/icons/Search.png'
import help from '../../assets/icons/Help.png'
import ProfileComponent from '../profile/ProfileComponent'

function SidebarComponent(props) {
  const [sidebarState, setSidebarState] = useState(false)

  const toggleSidebar = (state) => {
    setSidebarState(state)
    props.toggleSidebar(state)
  }

  return (
    <div className="position-relative vh-100 sf-pro-text">
      <div className={styles.vaiSidebarHeader}>
          <img className={sidebarState ? styles.vaiLogo : null} src={sidebarState ? VaiLogo : VirtualAiLogo} alt="VirtualAiLogo" />
      </div>
      <div className={styles.vaiSidebarList}>
        <NavLink
          to="/encore-monitoring"
          className={`${styles.vaiLink} nav-item nav-link px-0 py-2`}
          activeClassName={styles.vaiActiveLink}
        >
          <img className={sidebarState ? styles.vaiCenterText : null} src={monitoring} alt="MonitoringIcon" />
          <span className={`${sidebarState ? styles.vaiHideText : null} mx-3`}>
            Monitoring
          </span>
        </NavLink>
        <NavLink
          to="/encore-monitoring"
          className={`${styles.vaiLink} nav-item nav-link px-0 py-2 position-relative`}
        >
          <img className={sidebarState ? styles.vaiCenterText : null} src={analytics} alt="AnalyticsIcon" />
          <span
            className={`${styles.vaiChevRight} ${
              sidebarState ? styles.vaiHideText : null
            } mx-3`}
          >
            Analytics
          </span>
        </NavLink>
        <NavLink
          to="/encore-monitoring"
          className={`${styles.vaiLink} ${
            sidebarState ? styles.vaiHideText : null
          } nav-item nav-link px-0 py-2 position-relative`}
        >
          <img className={sidebarState ? styles.vaiCenterText : null} src={workers} alt="WorkersIcon" />
          <span
            className={`${styles.vaiChevRight} ${
              sidebarState ? styles.vaiHideText : null
            } mx-3`}
          >
            Workers
          </span>
        </NavLink>
        <NavLink
          to="/encore-monitoring"
          className={`${styles.vaiLink} nav-item nav-link px-0 py-2 position-relative`}
        >
          <img className={sidebarState ? styles.vaiCenterText : null} src={credentials} alt="CredentialsIcon" />
          <span
            className={`${styles.vaiChevRight} ${
              sidebarState ? styles.vaiHideText : null
            } mx-3`}
          >
            Credentials
          </span>
        </NavLink>
        <NavLink
          to="/encore-monitoring"
          className={`${styles.vaiLink} nav-item nav-link px-0 py-2 position-relative`}
        >
          <img className={sidebarState ? styles.vaiCenterText : null} src={process} alt="ProcessIcon" />
          <span
            className={`${styles.vaiChevRight} ${
              sidebarState ? styles.vaiHideText : null
            } mx-3`}
          >
            Process
          </span>
        </NavLink>
        <NavLink
          to="/encore-monitoring"
          className={`${styles.vaiLink} nav-item nav-link px-0 py-2 position-relative`}
        >
          <img className={sidebarState ? styles.vaiCenterText : null} src={settings} alt="SettingsIcon" />
          <span
            className={`${styles.vaiChevRight} ${
              sidebarState ? styles.vaiHideText : null
            } mx-3`}
          >
            Settings
          </span>
        </NavLink>
        <NavLink
          to="/encore-monitoring"
          className={`${styles.vaiLink} nav-item nav-link px-0 py-2 position-relative`}
        >
          <img className={sidebarState ? styles.vaiCenterText : null} src={help} alt="HelpIcon" />
          <span className={`${sidebarState ? styles.vaiHideText : null} mx-3`}>
            Help
          </span>
        </NavLink>
        <div
          className="input-group pt-3"
          style={{ display: sidebarState ? 'none' : null }}
        >
          <div className={`${styles.vaiSearchIcon} input-group-prepend`}>
            <img className={sidebarState ? styles.vaiCenterText : null} src={search} alt="SearchIcon" />
          </div>
          <input
            type="text"
            style={{ textIndent: '5px' }}
            className={`${styles.vaiSidebarSearch} form-control`}
            placeholder="Search"
            aria-label="Search"
            aria-describedby="search-addon"
          />
        </div>
      </div>
      <div className="position-absolute bottom-0">
        <ProfileComponent toggleSidebar={state => toggleSidebar(state)} />
      </div>
    </div>
  )
}

export default SidebarComponent
