import React, { useState } from 'react'
import { Route } from 'react-router'
import MonitoringPage from '../pages/monitoring/MonitoringPage'
import SidebarComponent from '../shared/sidebar/SidebarComponent'
import styles from './Views.module.css'

function ViewsComponent() {
  const [sidebarState, setSidebarState] = useState()

  return (
    <div className={styles.views}>
      <div
        className={`${styles.sidebar} ${
          sidebarState ? styles.vaiShrinkSidebar : null
        }`}
      >
        <SidebarComponent toggleSidebar={(state) => setSidebarState(state)} />
      </div>
      <div className={styles.content}>
        <Route path="/encore-monitoring" component={MonitoringPage} exact />
      </div>
    </div>
  )
}

export default ViewsComponent
