import React from 'react'
import styles from './Login.module.css'
import { useHistory } from 'react-router-dom'
import VirtualaiLogo from '../../assets/icons/VirtualAiLogo.png'

function LoginPage() {
  const history = useHistory()

  const handleLogin = () => {
    let path = `/encore-monitoring`; 
    history.push(path);
  }

  return (
    <div className={styles.vaiLoginBody}>
      <div className={styles.vaiLoginPanel}>
        <div className="container">
          <div className="row py-3">
            <div className="col-12 text-center py-4">
              <img
                src={VirtualaiLogo}
                alt="VirtualAiLogo"
                className={styles.vaiLoginLogo}
              />
              <p className={styles.vaiSignInText}>Sign In</p>
              <input
                type="text"
                className={styles.vaiLoginUserInput}
                placeholder="Email"
              />
              <br />
              <input
                type="password"
                className={styles.vaiLoginPasswordInput}
                placeholder="Password"
              />
              <button className={styles.vaiSignInButton} onClick={handleLogin}>
                Sign In
              </button>
              <p className={styles.vaiForgotPasswordText}>Forgot password?</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default LoginPage
